import React from 'react';

const Footer = () =>{
    let auth = localStorage.getItem("authUser");
    return auth?(
        <FooterWithAuth/>     
    ):(
        <FooterWithNonAuth/>
    )
}

const FooterWithAuth = () => (
    <footer className="footerauth footer">
      <div className="container">
        <span className="text-muted">Flip Book</span>
      </div>
    </footer>
)
    
const FooterWithNonAuth = () => (
    <footer className="footer">
      <div className="container">
        <span className="text-muted">Flip Book</span>
      </div>
    </footer>
)

export default Footer

