import React,  { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import { useLocation } from 'react-router';
import 'react-notifications/lib/notifications.css';


const Header = ()=>{
    let auth = localStorage.getItem("authUser")
    return auth?(
        <HeaderWithAuth/>  
    ):(
        <HeaderWithNonAuth/>
    )
}


const HeaderWithAuth = () =>{
    let location = useLocation()
    const [isToggle, toggle] = useState(false);
    const toggleMenu = () => toggle(!isToggle);

    useEffect(() => {
       toggle(isToggle=>!isToggle)
    },
    [location]
  );

  return (
    <nav className="navbar navbar-expand-md fixed-top navbar-dark bg-dark">
        
        <Link className="navbar-brand" to="/">Flip Book</Link>
        <button onClick={toggleMenu} className="navbar-toggler p-0 border-0" type="button" data-toggle="offcanvas">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className={`navbar-collapse offcanvas-collapse ${isToggle?'open':''}`} id="navbarsExampleDefault">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item active d-md-none">
              <Link className="nav-link" to="/dashboard">Flip book</Link>
            </li>
            <li className="nav-item active d-md-none">
              <Link className="nav-link" to="/profile">My Profile</Link>
            </li>
            <li className="nav-item active">
              <Link onClick={()=>localStorage.removeItem('authUser')} className="nav-link" to="/">Signout</Link>
            </li>
          </ul>
        </div>
      </nav>
  )
}

const HeaderWithNonAuth = () =>{
    const [isToggle, toggle] = useState(false);
    const toggleMenu = () => toggle(!isToggle)
    let location = useLocation()
    useEffect(() => {
        toggle(isToggle=>!isToggle)
     },[location]);

      return (
        <nav className="navbar navbar-expand-md fixed-top navbar-dark bg-dark">
            <Link className="navbar-brand" to="/">Flip Book</Link>
            <button onClick={toggleMenu} className="navbar-toggler p-0 border-0" type="button" data-toggle="offcanvas">
              <span className="navbar-toggler-icon"></span>
            </button>
      
            <div className={`navbar-collapse offcanvas-collapse ${isToggle?'open':''}`} id="navbarsExampleDefault">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <Link className="nav-link" to="/signin">Signin</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/signup">Signup</Link>
                </li>
              </ul>
            </div>
         </nav>
      )
}
    


export default Header