import React, { lazy } from 'react';
import * as ROUTE from './routesConstant';
import { Switch, Route } from 'react-router-dom';
const SignIn = lazy(() => import('../pages/SignIn/SignIn'));
const SignUp = lazy(() => import('../pages/SignUp/SignUp'));
const VerifyEmail = lazy(() => import('../pages/VerifyEmail/VerifyEmail'));
const ResetPass = lazy(() => import('../pages/ResetPass/ResetPass'));
const Pagenotfound = lazy(() => import('../pages/PageNotFound/Pagenotfound'));
const Dashboard = lazy(() => import('../pages/Dashboard/Dashboard'));
const ForgetPassword = lazy(() => import('../pages/ForgetPassword/ForgetPassword'));
const FlipBook = lazy(()=>import('../pages/FlipBook/FlipBook'));
const AddDocument = lazy(()=>import('../pages/Dashboard/AddDocument/AddDocument'));
const Profile = lazy(()=>import('../pages/Profile/Profile'));
const Cms     = lazy(()=>import('../pages/Cms/Cms'));
const AddCms  = lazy(()=>import('../pages/Cms/AddCms'));
const ViewCms = lazy(()=>import('../pages/Cms/ViewCms'));
const EditCms = lazy(()=>import('../pages/Cms/EditCms'));
const Photobooth = lazy(()=>import('../pages/Photobooth/Photobooth'));
const EditDocument = lazy(()=>import('../pages/Dashboard/EditDocument/EditDocument'));
const FlipBookDemo = lazy(()=>import('../pages/FlipBook/FlipBookDemo'));
const FlipBookVideoPreview = lazy(()=>import('../pages/FlipBook/FlipBookVideoPreview'));
const TakePhoto = lazy(()=>import('../pages/TakePhoto/TakePhoto'));

const Router = () => {
    return (
        <Switch>
            <Route exact path={ROUTE.HOME} component={SignIn}/>
            <Route exact path={ROUTE.SIGN_IN} component={SignIn}/>
            <Route exact path={ROUTE.SIGN_UP} component={SignUp}/>
            <Route exact path={ROUTE.VERIFYEMAIL} component={VerifyEmail}/>
            <Route exact path={ROUTE.RESETPASSWORD} component={ResetPass}/>
            <Route exact path={ROUTE.DASHBOARD} component={Dashboard}/>
            <Route exact path={ROUTE.FORGETPASS} component={ForgetPassword}/>
            <Route exact path={ROUTE.RESETPASS} component={ResetPass}/>
            <Route exact path={ROUTE.FLIPBOOK} component={FlipBook} />
            <Route exact path={ROUTE.FLIPBOOKDEMO} component={FlipBookDemo} />
            <Route exact path={ROUTE.FLIPBOOKVIDEOPREVIEW} component={FlipBookVideoPreview}/>
            <Route exact path={ROUTE.ADDFLIPBOOK} component={AddDocument}/>
            <Route exact path={ROUTE.EDITFLIPBOOK} component={EditDocument}/>
            <Route exact path={ROUTE.PROFILE} component={Profile}/>
            <Route exact path={ROUTE.CMS} component={Cms}/>
            <Route exact path={ROUTE.ADDCMS} component={AddCms}/>
            <Route exact path={ROUTE.EDITCMS} component={EditCms}/>
            <Route exact path={ROUTE.VIEWCMS} component={ViewCms}/>
            <Route exact path={ROUTE.PHOTOBOOTH} component={Photobooth}/>
            <Route exact path={ROUTE.TAKEPHOTO} component={TakePhoto}/>
            <Route path='*' exact={true} component={Pagenotfound} />
        </Switch>
    )
}

export default Router;