import React from 'react';
import Header from './Header';
import Footer from './Footer';
import Sidebar from './Sidebar';
import Route from '../../routes';

const nonauthroute = [
    'signup',
    'signin',
    'verify',
    'reset',
    'forgetpass',
    'flipbookdemo',
    'flipbookvideopreview'
]

const Layout = () => {
     let auth = localStorage.getItem('authUser');
     return auth?(
         <LayoutWithAuth/>
     ):(
         <LayoutWithNonAuth/>
     )
}

const LayoutWithAuth = () => {
    return  nonauthroute.includes(window.location.pathname.split('/')[1])?(
                <React.Fragment>
                    {window.location.pathname.split('/')[1]!=="flipbookvideopreview"?(<Header/>):null}
                        <main role="main" className="container" style={{minHeight:"100vh"}}>
                            <Route/>
                        </main>
                        {window.location.pathname.split('/')[1]!=="flipbookvideopreview"?(<Footer/>):null}
                    
               </React.Fragment>
            ):(
                <React.Fragment>
                    {window.location.pathname.split('/')[1]!=="flipbookvideopreview"?(<Header/>):null}
                        <div className="container-fluid">
                            <div className="row">
                                <Sidebar/>
                                <main role="main" className="col-md-9 ml-sm-auto col-lg-10 pt-3 px-4" style={{minHeight:"100vh"}}>
                                        <Route/>
                                </main>  
                            </div>
                        </div>
                        {window.location.pathname.split('/')[1]!=="flipbookvideopreview"?(<Footer/>):null}
                </React.Fragment>
            )
}

const LayoutWithNonAuth = () => (
    <React.Fragment>
        {window.location.pathname.split('/')[1]!=="flipbookvideopreview"?(<Header/>):null}
            <main role="main" className="container" style={{minHeight:"100vh"}}>
                  <Route/>
            </main>
            {window.location.pathname.split('/')[1]!=="flipbookvideopreview"?(<Footer/>):null}
    </React.Fragment>
)

export default Layout